const glosy2019Procent = glosy2019.map((arr) => {
  const dzielnik = (arr[0] + arr[1] + arr[2] + arr[3] + arr[4]) / 100;
  return {
    KO: arr[0] / dzielnik,
    KONF: arr[1] / dzielnik,
    PSL: arr[2] / dzielnik,
    PIS: arr[3] / dzielnik,
    SLD: arr[4] / dzielnik,
  };
});

export const glosy2019WgOkregu = {};
let i = 0;
glosy2019Procent.forEach((wynik) => {
  glosy2019WgOkregu[okregi[i].miasto] = {
    KO: glosy2019[i][0],
    KONF: glosy2019[i][1],
    PSL: glosy2019[i][2],
    PIS: glosy2019[i][3],
    SLD: glosy2019[i][4],
  };
  PodzialMandatowWOkregu(wynik, okregi[i], { log: true });
  i++;
});
